/* Scroll Bar */

/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #212529;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #00bf64;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

/* Header */
.wrapper {
  height: auto;
  width: 100%;
  background-color: rgba(0, 0, 0, .455) !important;
}

.header {
  background-color: rgba(0, 0, 0, .455) !important;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 0px 10px;
  z-index: 300;
}

.scrolled {
  box-shadow: 5px 5px 5px hsla(0, 0%, 49%, .2);
  background-color: #fff;
  position: fixed;
  top: 0;
  width: 100%;
  padding: 0px 10px;
  z-index: 300;
}

.logo-container {
  width: 180px;
}

.logo-container img {
  height: auto;
  max-width: 100%;
}

.headerItems {
  display: flex;
  justify-content: space-between;
  padding: 9px 9.5rem;
  align-items: center;
}

.items {
  display: flex;
  color: #fff;
  font-size: 1.0rem;
  font-weight: 500;
  transition: color .4s ease;
  cursor: pointer;
}

.items:hover {
  color: #ef2917 !important;
}

.scrolled_items {
  display: flex;
  justify-content: space-between;
  color: #000;
  font-family: var(--bs-font-archivo);
  font-size: 1.0rem;
  font-weight: 500;
  transition: color .4s ease;
  cursor: pointer;
}

.scrolled_items:hover {
  color: #ef2917 !important;
}

.header-content {
  color: white;
  display: flex;
  flex-direction: column;
  font-family: white;
  height: auto;
  justify-content: center;
  left: 0;
  overflow: hidden;
  padding-bottom: 5rem;
  padding-top: 14rem;
  position: relative;
  right: 0;
  text-align: center;
}

.header-content .wrapOne h2 {
  animation: mainHeader 1.5s linear;
  font-size: 3.2rem;
  font-weight: 700;
  letter-spacing: 0;
  margin: auto auto 1.8rem;
  max-width: 1100px;
  position: relative;
  width: 79%;
}

.header-content .wrapTwo p {
  animation: logoanimate 1.8s linear;
  font-size: 1.25rem;
  font-weight: 500;
  margin: 0 auto;
  position: relative;
}

.LogoContainer {
  animation: logoanimate 1.9s linear;
  bottom: 0;
  position: relative;
  width: 200px;
}

.amazon_logo {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  gap: 0.8rem;
  justify-content: center;
  margin-top: 1.5rem;
}

.amazon_spn_logo {
  width: 100%;
}

.LogoContainer img {
  border-radius: 0.5rem;
  height: 50px;
  object-fit: cover;
}


/* Dashboard */
.main-content {
  position: relative;
  background-color: rgba(4, 14, 51, .682);
}

.bg-image-container {
