/* Animation */
@keyframes logoanimate {
  0% {
    bottom: -350px;
    visibility: hidden;
  }

  100% {
    bottom: 0;
    visibility: visible;
  }

}

@keyframes mainHeader {
  0% {
    bottom: -200px;
    opacity: .2;
  }

  100% {
    bottom: 0;
    opacity: 1;
  }
}

@keyframes cardAnimation {
  0% {
    bottom: -80px;
    visibility: hidden;
  }

  100% {
    bottom: 0;
    visibility: visible;
  }

}

/* Accordion */
.accordion {
  display: flex;
  flex-direction: column;
}

.accordionItem {
  background-color: #fff;
  border-radius: 1.2rem;
  color: #000;
  cursor: pointer;
  font-size: 1.06rem;
  padding: 1rem 2rem;
  position: relative;
  z-index: 90;
}

.accordion-title {
  width: 70%;
  padding: 20px;
  border-radius: 20px;
  background: #fff;
  outline: none;
  border: none;
  text-align: left;
  color: #000;
  cursor: pointer;
  font-size: 1.06rem;
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-content: center;
  margin: 10px auto;
}

.accordion-content {
  display: none;
  animation: faqs .5s ease;
  background: transparent;
  color: #fff;
  overflow: hidden;
  padding: 0px 11.8rem;
  position: relative;
  text-align: left;
  transition: all 35s ease-in-out;
  -webkit-transition: .3s;
  z-index: 0;
}

.accordion-content p {
  font-size: .98rem;
  transition: all 3.5s ease-in-out;
  font-weight: 600;
}

.accordion-item.active .accordion-content {
  display: block;
}

@keyframes faqs {
  0% {
    top: -53px;
    opacity: 0;
  }

  100% {
    top: 0;
    opacity: 1;
  }
}

.scrollButton {
  border-radius: 50%;
  cursor: pointer;
  border: none;
  outline: none;
  width: 2rem;
  height: 2rem;
  position: fixed;
  bottom: 60px;
  right: 50px;
  background: #00bf64;
  color: white;
  box-shadow: 0 0 7px #05103b;
}

