/* SNACKBAR */
.snackbar {
  position: fixed;
  top: 20px;
  right: 80px;
  background-color: #f5f5f5;
  padding: 10px 20px;
  border-radius: 4px;
  z-index: 999;
  width: 20%;
  /* Ensure it's on top of other content */
  /* animation: snackbar-fadeIn 0.3s ease-in, snackbar-fadeOut 0.3s ease-out; Optional animation */
}

@keyframes snackbar-fadeIn {
  from {
    right: -200;
    opacity: 0;
  }

  to {
    right: 0;
    opacity: 1;
  }
}

@keyframes snackbar-fadeOut {
  from {
    right: 0;
    opacity: 1;
  }

  to {
    right: -200;
    opacity: 0;
  }
}

button {
  cursor: pointer;
}

